.dashboard-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-family: Arial, sans-serif;
    color: #333;
  }
  
  .dashboard-container h2 {
    margin-bottom: 20px;
    color: #0078d7;
  }
  
  .task-form {
    display: flex;
    margin-bottom: 20px;
  }
  
  .task-form input {
    flex-grow: 1;
    padding: 10px;
    margin-right: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .task-form button {
    padding: 10px;
    background-color: #0078d7;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .task-form button:hover {
    background-color: #0056b3;
  }
  
  .task-list {
    list-style-type: none;
    padding: 0;
  }
  
  .task-list li {
    padding: 10px;
    margin-bottom: 10px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .task-done {
    text-decoration: line-through;
    color: #ccc;
    cursor: pointer;
  }
  