/* loginPage.css */

/* Container */
.container {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

/* Heading */
h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #333;
}

/* Form */
form {
  display: flex;
  flex-direction: column;
}

/* Form inputs */
label {
  margin-bottom: 5px;
  font-weight: bold;
  color: #333;
}

input[type="text"],
input[type="password"] {
  padding: 8px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

/* Submit button */
button[type="submit"] {
  padding: 10px 20px;
  background-color: #0078D7;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #005A9E;
}
