/* userDashboard.css */

/* Container */
.dashboard-container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  /* Heading */
  h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
  }
  
  /* Task List */
  .task-list {
    list-style: none;
    padding: 0;
  }
  
  .task-list li {
    margin-bottom: 15px;
  }
  
  .task-list li div {
    margin-bottom: 5px;
  }
  
  .task-list li label {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .task-list li input[type="checkbox"] {
    margin-right: 5px;
  }
  
  .task-list li input[type="checkbox"]:checked + span {
    text-decoration: line-through;
  }
  
  